.selectedcat_MainDiv{
    width: 95%;
    color: black;
    padding: 2px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    z-index: 1;
    box-shadow: 0px 0px 2px;
    margin: auto;
}

.scmain{
    width: 99%;
    display: grid;
  grid-template-columns: 50% 50%;
  grid-row-gap: 10px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;

}


.selectedcat_img{
    width: 100%;
    height: 150px;
    margin: auto;
}

.selectedcat_name{
    margin-top: 5px;
    margin-bottom: -4px;
    margin-left: 5px;
    text-align: start;
    font-size: 15px;
}

.selectedcat_pricTxt{
    padding-left: 5px;
    text-align: start;
}
.selectedcat_AddBtnDiv{
    background-color: rgb(241, 241, 241);
    text-align: center;
}

.selectedcat_kg{
    text-align: start;
    margin-left: 5px;
}

.selectedcatBtn{
    cursor: pointer;
    background-color: orange;
    border: 0.5px solid;
    margin-left: auto;
    margin-right: auto;
    padding: 5px;
    padding-left: 50px;
    padding-right: 50px;
    margin-bottom: 5px;
}