.m_MainDiv{
    display: flex;
    background-color: white;
    margin-top: 15px;
    margin-right: 15px;
    margin-bottom: 15px;
}

.m_title{
    font-weight: bold;
    margin-top: 10px;
}

.m_info{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
}

.m_img{
    object-fit: contain;
    width: 180px;
    height: 180px;
}

.pButton{
    margin-top: 10px;
}

.nummmDiv{
    display: grid;
    grid-template-columns: 1fr;
    text-align: end;
}

.dleImg{
    text-align: end;
}