.t_MainDiv{
    display: grid;
    background-color: white;
    margin: 10px;
    box-shadow: 0px 0px 5px;
    padding: 10px;
    grid-template-columns: 50% 50%;
}

.total_txt{
    margin-bottom: -10px;
    color: green;
    text-align: end;
    font-weight: bold;
}

.t_hr1{
    margin-top: -10px;
    grid-column: 1/-1;
}

.t_proceedBtn{
    grid-column: 2;
    color: white;
    background-color: green;
    border: 0px;
    border-radius: 55px;
}